<template>
  <div>
    <b-card>
      <b-media no-body>
        <b-media-aside>
          <b-avatar
            pill
            size="42"
            src="/demo/vuexy-vuejs-admin-dashboard-template/demo-4/img/avatar-s-9.397f0acd.jpg"
          />
        </b-media-aside>
        <b-media-body>
          <b-form-input
            readonly
            size="lg"
            class="rounded-pill text-secondary cursor-pointer"
            value="Get your product at worth price"
            style="font-size: 1rem"
            @click="showModal"
          />
        </b-media-body>
      </b-media>
      <div class="d-flex justify-content-around pt-2">
        <b-button
          v-ripple.400="'rgba(113, 102, 240, 0.15)'"
          variant="flat-secondary"
        >
          <feather-icon
            icon="TagIcon"
            class="mr-50 text-success"
          />
          <span class="align-middle">Product</span>
        </b-button>
        <b-button
          v-ripple.400="'rgba(255, 255, 255, 0.15)'"
          variant="flat-secondary"
        >
          <feather-icon
            icon="ImageIcon"
            class="mr-50 text-info"
          />
          <span class="align-middle">Photo</span>
        </b-button>
        <b-button
          v-ripple.400="'rgba(40, 199, 111, 0.15)'"
          variant="flat-secondary"
        >
          <feather-icon
            icon="VideoIcon"
            class="mr-50 text-danger"
          />
          <span class="align-middle">Live Video</span>
        </b-button>
        <b-button variant="flat-secondary">
          <feather-icon
            icon="BarChart2Icon"
            class="mr-50 text-primary"
          />
          <span class="align-middle">Poll</span>
        </b-button>
        <b-button
          v-ripple.400="'rgba(113, 102, 240, 0.15)'"
          variant="flat-secondary"
        >
          <feather-icon
            icon="StarIcon"
            class="mr-50 text-warning"
          />
          <span class="align-middle">Review</span>
        </b-button>
      </div>
    </b-card>
    <b-modal
      id="pool-modal"
      ref="pool-modal"
      title="Create a pool"
      size="lg"
      ok-only
      ok-title="Accept"
    >
      <b-media vertical-align="top">
        <template #aside>
          <b-avatar
            pill
            size="64"
            :src="require('@/assets/images/portrait/small/avatar-s-3.jpg')"
          />
        </template>
        <h4 class="media-heading">
          Cookie candy
        </h4>
        <b-button
          v-ripple.400="'rgba(113, 102, 240, 0.15)'"
          variant="outline-secondary"
          pill
          size="sm"
        >
          <feather-icon
            icon="GlobeIcon"
            class="mr-50"
          />
          <span class="align-middle">Anyone</span>
          <feather-icon
            icon="ChevronDownIcon"
            class="ml-50"
          />
        </b-button>
      </b-media>
      <b-form-textarea
        v-if="postType === EMPTY_POST"
        id="textarea-auto-height"
        class="mt-2 mb-1"
        placeholder="Auto height textarea"
        rows="3"
        max-rows="10"
      />
      <discover-search-product v-if="postType === PRODUCT_POST" />

      <template #modal-footer>
        <div class="w-100 d-flex justify-content-between">
          <div class="">
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              v-b-tooltip.hover.top="'Search a product'"
              variant="flat-dark"
              class="btn-icon"
              pill
              @click="postType = PRODUCT_POST"
            >
              <feather-icon
                icon="TagIcon"
                size="20"
              />
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              v-b-tooltip.hover.top="'Add a photo'"
              variant="flat-dark"
              class="btn-icon"
              pill
            >
              <feather-icon
                icon="ImageIcon"
                size="20"
              />
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              v-b-tooltip.hover.top="'Start a live video'"
              variant="flat-dark"
              class="btn-icon"
              pill
            >
              <feather-icon
                icon="VideoIcon"
                size="20"
              />
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              v-b-tooltip.hover.top="'Create a poll'"
              variant="flat-dark"
              class="btn-icon"
              pill
            >
              <feather-icon
                icon="BarChart2Icon"
                size="20"
              />
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              v-b-tooltip.hover.top="'Write a review'"
              variant="flat-dark"
              class="btn-icon"
              pill
            >
              <feather-icon
                icon="StarIcon"
                size="20"
              />
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              v-b-tooltip.hover.top="'Add to your pool'"
              variant="flat-dark"
              class="btn-icon"
              pill
            >
              <feather-icon
                icon="MoreHorizontalIcon"
                size="20"
              />
            </b-button>
          </div>
          <div>
            <b-button
              v-if="postType !== EMPTY_POST"
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              variant="primary"
              pill
              class="mr-2"
              @click="postType = EMPTY_POST"
            >
              Back
            </b-button>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              variant="primary"
              pill
              class=""
              disabled
              @click="show = false"
            >
              Post
            </b-button>
          </div>
        </div>
      </template>
    </b-modal>
  </div>
</template>
<script>
import {
  BAvatar,
  BCard,
  BButton,
  BMedia,
  BMediaAside,
  BMediaBody,
  BFormInput,
  BFormTextarea,
  BModal,
  VBModal,
  VBTooltip,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import DiscoverSearchProduct from './DiscoverSearchProduct.vue'

const EMPTY_POST = -1
const PRODUCT_POST = 1
// const IMAGE_POST = 2

export default {
  components: {
    BAvatar,
    BCard,
    BButton,
    BMedia,
    BMediaAside,
    BMediaBody,
    BFormInput,
    BFormTextarea,
    BModal,
    DiscoverSearchProduct,
  },
  directives: {
    'b-tooltip': VBTooltip,
    'b-modal': VBModal,
    Ripple,
  },
  data() {
    return {
      EMPTY_POST,
      PRODUCT_POST,
      postType: EMPTY_POST,
    }
  },
  methods: {
    showModal() {
      this.$refs['pool-modal'].show()
    },
    hideModal() {
      this.$refs['pool-modal'].hide()
    },
    toggleModal() {
      // We pass the ID of the button that we want to return focus to
      // when the modal has hidden
      this.$refs['pool-modal'].toggle('#toggle-btn')
    },
  },
}
</script>
